import React from "react";
import moment from "moment";
import ReactDatetime from "react-datetime";
import {
  Container,
  Button,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
} from "reactstrap";
import Loading from "components/Loading";
import cons from "../../../../constants";
import { fixNumber } from "helpers/UtilsHelper";
import {
  getManualInvoiceDetail,
  chargeInvoice,
} from "../../../../redux/actions/billingAction";
import "./CreateInvoice.scss";

import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

function InvoiceDetail(props) {
  const dispatch = useDispatch();
  const manualInvoiceData = useSelector((state) => state.billing.data);
  const loading = useSelector((state) => state.billing.loading);
  const invoiceId = props.match.params.id;
  const { invoiceNumber, date, comment, amount, id, status } =
    manualInvoiceData;

  useEffect(() => {
    dispatch(getManualInvoiceDetail(invoiceId));
  }, []);

  const goBack = () => {
    props.history.goBack();
  };

  const payInvoice = (invoiceId) => {
    dispatch(chargeInvoice(invoiceId));
  };

  return (
    <Container className="invoice-detail">
      {loading && <Loading />}
      <Card>
        <CardHeader>
          <Row className="d-flex">
            <Col md={6} xs={12} className="d-flex flex-column align-items-left">
              <h2>
                <b className="text-primary">EtheraPay</b>
              </h2>
              <small>Invoice: #{invoiceNumber}</small>
            </Col>
            <Col md={6} xs={12} className="mt-3 mt-md-0 text-right">
              <Button
                color="info"
                disabled={status !== cons.INVOICE_STATUS.UNPAID}
                onClick={() => payInvoice(id)}
              >
                Pay now
              </Button>
            </Col>
          </Row>
        </CardHeader>
        <CardBody>
          <Row className="tb-custom">
            <Col md={4} lg={3}>
              <Row className="header">
                <b>Date</b>
              </Row>
              <Row className="body">
                <ReactDatetime
                  className="rdt"
                  value={moment(date)}
                  inputProps={{
                    placeholder: "MM/DD/YYYY",
                  }}
                  closeOnSelect={true}
                  dateFormat={true}
                  timeFormat={false}
                  readOnly
                />
              </Row>
            </Col>
            <Col md={6} lg={7}>
              <Row className="header">
                <b>Comment</b>
              </Row>
              <Row className="body">
                <Input defaultValue={comment} readOnly />
              </Row>
            </Col>
            <Col md={2} lg={2}>
              <Row className="header">
                <b>Amount</b>
              </Row>
              <Row className="body">
                <InputGroup>
                  <Input defaultValue={amount} readOnly />
                  <InputGroupAddon addonType="append">
                    <InputGroupText>
                      <i className="fas fa-dollar-sign" />
                    </InputGroupText>
                  </InputGroupAddon>
                </InputGroup>
              </Row>
            </Col>
          </Row>
        </CardBody>
        <CardFooter>
          <Col sm={12} className="text-center">
            <span className="font-weight-bold">
              Total Amount Due: ${fixNumber(amount)}
            </span>
          </Col>
          <Row className="mt-3">
            <Col className="text-center">
              <Button color="secondary" onClick={goBack}>
                Go Back
              </Button>
            </Col>
          </Row>
        </CardFooter>
      </Card>
    </Container>
  );
}

export default InvoiceDetail;
